<template>
  <div
    :class="{ 'headroom--unpinned': scrolled }"
    class="
      fixed
      w-full
      h-12
      bg-black
      border-b border-gray-500
      px-5
      py-1
      z-60
      headroom
      header
      flex
      justify-center
      font-texto
    "
    v-on:scroll="handleScroll"
  >
    <div class="max-h-full flex-1">
      <img src="@/assets/logo.png" alt="" class="h-full w-auto" />
    </div>

    <div class="mx-4 flex items-center justify-center">
      <div class="text-xs border border-main text-main hover:bg-main hover:text-white cursor-pointer transition duration-300 px-1">
        ENG
      </div>
    </div>
    <div class="flex justify-center items-center font-title relative top-0.5">
      <a href="./#footer" class="text-white">Contacto</a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      limitPosition: window.innerHeight,
      scrolled: true,
      lastPosition: 0,
    };
  },

  methods: {
    handleScroll() {
      if (
        this.lastPosition < window.scrollY &&
        this.limitPosition < window.scrollY
      ) {
        this.scrolled = true;
        // abajo
      }
      this.scrolled = window.scrollY < this.limitPosition;

      if (
        this.lastPosition > window.scrollY &&
        window.scrollY > this.limitPosition
      ) {
        this.scrolled = false;
        // arriba
      }

      this.lastPosition = window.scrollY;
    },
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
};
</script>

<style scoped>
.headroom--pinned {
  transform: translateY(0%);
}
.headroom--unpinned {
  transform: translateY(-100%);
}
.headroom {
  will-change: transform;
  transition: transform 200ms linear;
}
</style>
